.content {
    text-align: left;
    padding-left: 6%;
    padding-right: 6%;
    font-weight: 400;
    display: block;
    box-sizing: border-box;
    position: relative;
}

.column {
    box-sizing: border-box;
    float: left;
}

div.column {
    width: 30%;
    padding-top: 2%;
    padding-left: 1%;
    padding-right: 2%;
}

.g-switch-btn {
    display:inline-block;
    padding:0.35em 1.2em;
    border:0.1em solid #000000;
    margin:0 0.3em 0.3em 0;
    border-radius:0.12em;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:300;
    color:#000000;
    text-align:center;
    transition: all 0.2s;
}

.g-switch-btn:hover {
    background-color:#FFFFFF;
}



